::-webkit-scrollbar-thumb {
  border-radius: 4px;
  padding: 5px;
  background-color: #fff !important;
  box-shadow: 0 0 1px rgba(255, 255, 255, 0.5);
}

body {
  font-size: 28px;
}

.main-container {
  display: flex;
  height: 100%;
  position: relative;
}

.main-left {
  background-color: rgb(29, 30, 32);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  text-align: center;
  width: 5vw;
}

.left-one {
  width: 100%;
  height: 20vh;
}

.fa-bars {
  background-color: #e84118;
  color: #fff;
  padding: 10px;
  width: 100%;
}

.left-two {
  height: 40vh;
  width: 100%;
  margin-bottom: 100%;
}

.left-two ul {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  height: 100%;
}

.left-two li {
  list-style: none;
  margin-top: 15px;
}

.active-li {
  color: #e84118 !important;
}

.active-li i {
  color: #e84118 !important;
  font-size: 150%;
}

.left-two a:link, .left-two a:visited {
  color: #fff;
}

.fade-enter {
  opacity: 0;
}

.fade-enter.fade-enter-active {
  opacity: 1;
  display: none;
}

.fade-exit {
  opacity: 1;
  /* display: none; */
}

.fade-exit.fade-exit-leave {
  opacity: 0;
}

@keyframes test-animation {
  0% {
    transform: scaleX(1);
  }
  50% {
    transform: scaleX(0);
  }
  100% {
    transform: scaleX(1);
  }
}

@keyframes text-fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

.main-middle {
  width: 30vw;
}

.main-container-img {
  background-image: linear-gradient(rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.2)), url("../static/download.png");
  background-size: cover;
  background-position: top;
  background-repeat: no-repeat;
  /* border: 3px solid rgb(29, 30, 32); */
  height: 100%;
  position: relative;
  transform: scaleY(1.05);
  width: 100%;
  /* z-index: -1; */
}

.main-container-img:active {
  transform: scaleY(1);
}

.main-right {
  animation: test-animation 0.75s;
  transform-origin: left;
  background-color: rgb(29, 30, 32);
  height: 100%;
  overflow: hidden;
  padding: 0 5%;
  position: relative;
  width: 65vw;
  overflow: auto;
  overflow-x: hidden;
}

.main-right p, .main-right h1, .main-right h2, .main-right h3, .main-right hr, .main-right div {
  animation: text-fade 0.7s;
}

.right-scrollable {
  width: 100%;
